import React from 'react';
import { useSpring, animated } from 'react-spring';

import clockViolet from '../../assets/images/clock-violet.svg';
import clockBlue from '../../assets/images/clock-blue.svg';
import './EventCard.css';

const EventCard = ({ evName, profName, time, groupNum }) => {

    let cardStyle = "card white-bg violet-card";
    // let clockStyle = "clock white-bg relative violet-clock";
    let clockTextStyle = "violet align-with-clock";
    if (groupNum === 2) {
        cardStyle = "card white-bg blue-card";
        // clockStyle = "clock white-bg relative blue-clock";
        clockTextStyle = "blue align-with-clock";
    }

    const [props, set] = useSpring(() => ({ s: 1, config: { mass: 5, tension: 350, friction: 40 } }));
    return <animated.div
        className={cardStyle}
        onMouseEnter={ () => set({ s: 1.1 }) }
        onMouseLeave={ () => set({ s: 1 }) }
        style={{ transform: props.s.interpolate((s) => `scale(${s})`) }}
    >
        <h2>{evName}</h2>
        <hr />
        <div style={{color: 'black'}}>
            <p style={{marginBottom: '.5em'}}>{profName}</p>
            <div className="flex-centered-vert">
                {/* <div className={clockStyle} /> */}
                <img src={(groupNum === 2) ? clockBlue : clockViolet} className="clock-icon" />
                <div className="align-with-clock" className={clockTextStyle}>{time}</div>
            </div>
            {/* Подробнее... */}
        </div>
    </animated.div>;

    // return (
    //     <div className={cardStyle}>
    //         <h2 style={{fontSize: '1.1em'}}>{evName}</h2>
    //         <hr />
    //         <div style={{color: 'black'}}>
    //         <p><span>М. В. Торопыгина</span> (ИКВиА НИУ ВШЭ)</p>
    //         <div className="flex-centered-vert">
    //             <div className={clockStyle} />
    //             <div className="align-with-clock" className={clockTextStyle}>10:00 - 11:00</div>
    //         </div>
    //         </div>
    //     </div>
    // );
}

export default EventCard;