import React, { useState } from 'react';

import logoAbout from '../../assets/images/logo-about.svg';
import './Form.css';

const Form = ({ showForm }) => {

    let formClasses = 'hide'
    if (showForm !== 0) {
        formClasses = 'form white-bg flex-column-centered-row show'
    }

    let checkboxesContainerStyle = 'flex-column checkboxes-cnt form-inputs-width show';
    if (showForm === 2) {
        checkboxesContainerStyle = 'hide'
    }

    let textareaContainerStyle = 'hide';
    if (showForm === 2) {
        textareaContainerStyle = 'flex-column relative input-margin-bottom'
    }

    let telContainerStyle = 'flex-column relative input-margin-bottom';
    if (showForm === 2) {
        telContainerStyle = 'hide'
    }
    // let formClasses = 'form-container hide'
    // if (show) {
    //     formClasses = 'form-container show-flex'
    // }

    const [submitting, setSubmitting] = useState(false);
    const [firstCheckboxSelected, setFirstCheckboxSelected] = useState(0);
    const [secondCheckboxSelected, setSecondCheckboxSelected] = useState(0);
    const [thirdCheckboxSelected, setThirdCheckboxSelected] = useState(0);

    let firstCheckboxStyleClasses = 'checkbox white-bg';
    if (firstCheckboxSelected === 1) {
        firstCheckboxStyleClasses = 'checkbox white-bg checked';
    } else if (firstCheckboxSelected === -1) {
        firstCheckboxStyleClasses = 'checkbox white-bg unchecked';
    }

    let secondCheckboxStyleClasses = 'checkbox white-bg';
    if (secondCheckboxSelected === 1) {
        secondCheckboxStyleClasses = 'checkbox white-bg checked';
    } else if (secondCheckboxSelected === -1) {
        secondCheckboxStyleClasses = 'checkbox white-bg unchecked';
    }

    let thirdCheckboxStyleClasses = 'checkbox white-bg';
    if (thirdCheckboxSelected === 1) {
        thirdCheckboxStyleClasses = 'checkbox white-bg checked';
    } else if (thirdCheckboxSelected === -1) {
        thirdCheckboxStyleClasses = 'checkbox white-bg unchecked';
    }

    const getButtonStyle = () => {
        if (submitting) {
            return ' checked';
        } else {
            return '';
        }
    }

    const clickFirstCheckmark = () => {
        if (firstCheckboxSelected === 0 || firstCheckboxSelected === -1) {
            setFirstCheckboxSelected(1);
        } else {
            setFirstCheckboxSelected(-1);
        }
    }

    const clickSecondCheckmark = () => {
        if (secondCheckboxSelected === 0 || secondCheckboxSelected === -1) {
            setSecondCheckboxSelected(1);
        } else {
            setSecondCheckboxSelected(-1);
        }
    }

    const clickThirdCheckmark = () => {
        if (thirdCheckboxSelected === 0 || thirdCheckboxSelected === -1) {
            setThirdCheckboxSelected(1);
        } else {
            setThirdCheckboxSelected(-1);
        }
    }

    return (
        <div className={formClasses}>

            <img src={logoAbout} alt="Лого" className="form-logo" />

            <div style={{marginTop: 10}} className="flex-column relative input-margin-bottom">
                <input type="text" className="form-inputs-width" required/>
                <label>Как вас зовут?</label>
            </div>

            <div className="flex-column relative input-margin-bottom">
                <input type="text" className="form-inputs-width" required/>
                <label>Электронная почта</label>
            </div>

            <div className={telContainerStyle}>
                <input type="tel" className="form-inputs-width" required/>
                <label>Номер телефона</label>
            </div>

            <div className={textareaContainerStyle}>
                <textarea type="" className="form-inputs-width" required/>
                <label>Ваш вопрос</label>
            </div>

            <div className={checkboxesContainerStyle}>
                <div className="flex-centered-vert">
                    <div className={firstCheckboxStyleClasses} onClick={clickFirstCheckmark}>
                        <div className="checkbox-checkmark relative" />
                    </div>
                    <p onClick={clickFirstCheckmark}>Открытие (27 Октября)</p>
                </div>
                <div className="flex-centered-vert">
                    <div className={secondCheckboxStyleClasses} onClick={clickSecondCheckmark}>
                        <div className="checkbox-checkmark relative" />
                    </div>
                    <p onClick={clickSecondCheckmark}>Первый День (2 Ноября)</p>
                </div>
                <div className="flex-centered-vert">
                    <div className={thirdCheckboxStyleClasses} onClick={clickThirdCheckmark}>
                        <div className="checkbox-checkmark relative" />
                    </div>
                    <p onClick={clickThirdCheckmark}>Последний День (3 Ноября)</p>
                </div>
            </div>

            <div className={"button-wrapper" + getButtonStyle()}>
                <div className="submit form-inputs-width grad-bg pointer" onClick={() => setSubmitting(!submitting)}>
                    {showForm === 2 ? 'Спросить' : 'Записаться'}
                </div>
                <div className="loader loader-violet" />
                <div className="loader loader-blue" />
                <div className="check-wrapper">
                    <div className="checkmark" />
                </div>
            </div>
        </div>
    );
}

export default Form;