import React, { useState } from 'react';
import Img from 'react-image';

import logoHome from '../../assets/images/logo-home.svg';
import Spinner from '../Spinner/Spinner'
import './Home.css';

const Home = ({ buttonClick }) => {

    const [buttonHovered, setButtonHovered] = useState(false);
    let spanStyles = 'hide';
    if (buttonHovered) {
        spanStyles = 'show';
    }

    return (
        <div className="container padded-container home-cnt">
            <Img src={logoHome} className="logo-home" loader={<Spinner where='home' />} />
            <h1 className="head">Юкарихон: стрелы и строфы</h1>
            <p className="subhead">городская школа японистики в Москве</p>
            <p className="subhead">27 октября — 3 ноября</p>
            <a className="text-centered pointer"
                onMouseEnter={() => setButtonHovered(true)}
                onMouseLeave={() => setButtonHovered(false)}
                onClick={() => {
                    buttonClick(1);
                }}>
                    <span className={spanStyles}></span>
                    <span className={spanStyles}></span>
                    <span className={spanStyles}></span>
                    <span className={spanStyles}></span>
                    Записаться
            </a>
            <div className="mousey">
                <div className="scroller"></div>
            </div>
        </div>
    );
}

export default Home;