import React, { useState, useEffect } from 'react';
import { Element } from 'react-scroll';

import Nav from './components/Nav/Nav';
import Home from './components/Home/Home';
import About from './components/About/About';
import Events from './components/Events/Events';
import Contacts from './components/Contacts/Contacts';
import Faq from './components/Faq/Faq';
import Backdrop from './components/Backdrop/Backdrop';
import SideDrawer from './components/SideDrawer/SideDrawer';
import Form from './components/Form/Form';
import './App.css';

const App = () => {
    const [sideDrawerOpen, setSideDrawerOpen] = useState(false);
    const [backdropOpen, setBackdropOpen] = useState(false);
    const [showForm, setShowForm] = useState(0);

    const drawerToggleClickHandler = () => {
        setSideDrawerOpen(!sideDrawerOpen);
        setBackdropOpen(!backdropOpen);
    };

    const closeToggleClickHandler = () => {
        setSideDrawerOpen(false);
        setBackdropOpen(false);
        setShowForm(0);
    };

    const openForm = (formType) => {
        setBackdropOpen(true);
        setShowForm(formType);
    }

    const [prevScrollpos, setPrevScrollpos] = useState(window.pageYOffset);
    const [visible, setVisible] = useState(true);
    const [shadowToNav, setShadowToNav] = useState(false);

    let headerShadowStyle = "";

    const handleScroll = () => {
        const currentScrollPos = window.pageYOffset;
        const visible = prevScrollpos > currentScrollPos;

        if (window.scrollY > 50) {
            setShadowToNav(true);
        } else {
            setShadowToNav(false);
        }

        setPrevScrollpos(currentScrollPos);
        setVisible(visible);
    };

    useEffect(() => {
        window.addEventListener('scroll', handleScroll);
        return () => {
            window.removeEventListener('scroll', handleScroll);
        }
    });

    if (backdropOpen) {
        document.body.style.overflow = 'hidden';
    } else {
        document.body.style.overflow = 'visible';
    }

    // let backdrop;
    // if (sideDrawerOpen) {
    //   backdrop = <Backdrop click={backdropClickHandler} />;
    // }
    return (
        <div>
            <Nav drawerToggleClickHandler={drawerToggleClickHandler} show={visible} shadow={shadowToNav} />
            <SideDrawer closeButtonClick={closeToggleClickHandler} show={sideDrawerOpen} />
            <Backdrop show={backdropOpen} click={closeToggleClickHandler} />
            <Form showForm={ showForm } />
            <Element name="home" className="element">
                <Home buttonClick={openForm} />
            </Element>
            <Element name="about" className="element">
                <About />
            </Element>
            <Element name="events" className="element">
                <Events />
            </Element>
            <Element name="faq" className="element">
                <Faq buttonClick={openForm} />
            </Element>
            <Element name="contacts" className="element">
                <Contacts />
            </Element>
        </div>
    );
}

export default App;
