import React from 'react';

import exit from '../../assets/images/exit-white.svg';
import './Backdrop.css';

const Backdrop = ({ show, click }) => {
    let backdropClasses = 'hide'
    if (show) {
        backdropClasses = 'backdrop sticky show'
    }

    return (
        <div className={backdropClasses} onClick={click}>
            <img className="close-backdrop pointer" style={{width: 25, height: 25, cursor: 'pointer'}} src={exit} alt="Выйти" onClick={click} />
        </div>
    );
}

export default Backdrop;