import React, { useState, useCallback } from 'react';
import { useTransition, animated } from 'react-spring';

import Event from './Event';
import './Events.css';

const pages = [
    ({ style, arrowClicked }) => <animated.div style={style} className="relative">
        <Event
            evName="Открытие"
            day="Воскресенье"
            date="27 Октября 2019"
            arrowClicked={arrowClicked} />
    </animated.div>,
    ({ style, arrowClicked }) => <animated.div style={style} className="relative">
        <Event
            evName="Первый день"
            day="Суббота"
            date="2 Ноября 2019"
            arrowClicked={arrowClicked} />
    </animated.div>,
    ({ style, arrowClicked }) => <animated.div style={style} className="relative">
        <Event
            evName="Последний день"
            day="Воскресенье"
            date="3 Ноября 2019"
            arrowClicked={arrowClicked} />
    </animated.div>,
];

const Events = () => {

    const [eventIndex, setEventIndex] = useState(0);
    const [rightArrowClicked, setRightArrowClicked] = useState(false);

    const handleArrowsClick = (isRightArrow) => {
        if (isRightArrow) {
            setRightArrowClicked(true);
            setEventIndex((eventIndex + 1) % 3);
        } else {
            setRightArrowClicked(false);
            if (eventIndex === 0) {
                setEventIndex(2);
            } else {
                setEventIndex(eventIndex - 1);
            }
        }
    }

    const transitions = useTransition(eventIndex, item => item, {
        from: { opacity: 0, transform: rightArrowClicked ? 'translate3d(100%,0,0)' : 'translate3d(-100%,0,0)' },
        enter: { opacity: 1, transform: 'translate3d(0%,0,0)' },
        leave: { opacity: 0, transform: rightArrowClicked ?  'translate3d(-50%,0,0)' : 'translate3d(50%,0,0)', position: 'absolute' },
    });

    return (
    <div className="relative overflow-hidden full-width">
        {transitions.map(({ item, props, key }) => {
            const Page = pages[item];
            return <Page key={key} style={props} arrowClicked={handleArrowsClick} />;
        })}
    </div>
    );
}

export default Events;